import React, { useState } from 'react';
import { motion } from 'framer-motion';

interface YouTubePlayerProps {
  defaultVideoId?: string;
}

const YouTubePlayer: React.FC<YouTubePlayerProps> = ({ defaultVideoId = 'QGMFzltjygM' }) => {
  const [isLoading, setIsLoading] = useState(true);

  // Simulate loading state when component first mounts
  React.useEffect(() => {
    const timer = setTimeout(() => {
      setIsLoading(false);
    }, 1000);
    
    return () => clearTimeout(timer);
  }, []);

  return (
    <motion.div 
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
      className="w-full max-w-4xl mx-auto"
    >
      <motion.div 
        className="aspect-w-16 aspect-h-9 rounded-xl overflow-hidden shadow-xl"
        whileHover={{ scale: 1.02 }}
        transition={{ duration: 0.3 }}
      >
        {isLoading ? (
          <div className="w-full h-full flex items-center justify-center bg-gray-800">
            <div className="w-10 h-10 border-4 border-primary-500 border-t-transparent rounded-full animate-spin"></div>
          </div>
        ) : (
          <iframe
            src={`https://www.youtube.com/embed/${defaultVideoId}`}
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
            className="w-full h-full rounded-lg"
          ></iframe>
        )}
      </motion.div>
    </motion.div>
  );
};

export default YouTubePlayer; 